import view_video from '@/components/video/watch'
import forumobjct from "@/components/script/forumobjct";
import videoobject from "@/components/script/videoobject";
import comment_reply from "@/components/comment/reply";
import comment_list from "@/components/comment/list";
import view_watch_limit from '@/components/video/watch_limit'

export default {
  name: "video_view",
  mixins: [forumobjct, videoobject],
  components: {
    view_video,
    comment_reply,
    comment_list,
    view_watch_limit,
    releated_list: () => import('@/components/video/item/releated_list'),
    guesslike: () => import('@/components/video/item/guesslike')
  },
  data() {
    return {
      topic_data: undefined,
      forum_conf: undefined,
      edit: false,
      reletad_list: undefined
    }
  },
  computed: {
    is_manager: function () {
      try {
        return this.$store.state.user.userinfo.is_manager
      } catch (e) {

      }
      return false;
    },
    show_view_watch_limit: function () {
      return false;
      var look_list = this.$store.state.user_visit.get_look_list();
      console.log('[show_view_watch_limit]:', look_list);
      if (!this.$store.state.hasLogin && look_list.length > 1) {
        return true;
      }
      if (look_list && look_list.length >= 3 && !this.$store.state.user.userinfo.is_vip) {
        return true;
      }
      return false;
    },
  },
  created() {
    this.load_video();
  },
  methods: {
    load_video: function () {
      var cid = this.$route.params.id;
      var urlvars = this.getUrlVars();
      if (urlvars.edit) {
        console.log('处于编辑的帖子')
      }
      this.get_topic({
        id: cid,
        from_cache: this.from_cache(),
        releted: true,
        edit: urlvars.edit,
        cb: this.on_load_topic_data
      });
    },
    on_load_topic_data: function (topic_data, edit, reletad_list) {
      console.log("[on_load_topic_data]topic_data:", topic_data, ' edit:', edit);
      this.topic_data = topic_data;
      this.set_title(this.video_title());
      this.forum_conf = this.get_forum(topic_data.fid);
      this.save_topic_data(topic_data);

      if (this.topic_data.video_info && !this.show_view_watch_limit) {
        var online_video = this.topic_data.video_info.online_videos[0];
        console.log('online_video:', online_video);
        var hash_id = online_video.hash_id;
        this.$store.state.user_visit.push(hash_id);
      }

      if (edit) {
        this.edit = edit;
      }

      if (reletad_list) {
        this.reletad_list = reletad_list;
      }


    },
    get_carousel_threads: function () {
      try {
        if (this.$store.state.init.data.carousel_threads.datas.length > 0) {
          return this.$store.state.init.data.carousel_threads.datas;
        }
      } catch (e) {

      }
      return undefined
    }
  },
  watch: {
    '$route'(to, from) {
      this.topic_data = undefined;
      this.$nextTick(this.load_video);
    }
  },
}
